<template>
	<div class="tabs">
		<div class="tabs__header">
			<div
				class="tab"
				ref="tabs"
				:class="{ '_active': index === activeIndex }"
				v-for="(tab, index) in tabs"
				:key="index"
				@click="activeTab(index)"
			>
				{{ tab.title }}
			</div>
		</div>
		<div class="tabs__body">
			<div
				class="tab-content"
				v-show="index === activeIndex"
				v-for="(tab, index) in tabs"
				:key="index"
			>
				<slot :name="tab.name"></slot>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Tabs",

		props: {
			tabs: {
				type: Array,
				required: true,
			}
		},

		data() {
			return {
				activeIndex: 0,
			}
		},

		methods: {
			/**
			 * @param index
			 */
			activeTab(index) {
				this.activeIndex = index;
			},
			nextTab() {
				if (this.activeIndex < this.tabs.length - 1) {
					this.activeIndex++;
				}
			},
			previousTab() {
				if (this.activeIndex > 0) {
					this.activeIndex--;
				}
			},
		},

		mounted() {
			app.$handler.add([
				{ id: 'tabs_next_tab', keycode: [app.$handler.codes.TAB], callback: this.nextTab },
				{ id: 'tabs_prev_tab', keycode: [app.$handler.codes.SHIFT, app.$handler.codes.TAB], callback: this.previousTab },
			]);
		},

		beforeDestroy() {
			app.$handler.remove(['tabs_next_tab', 'tabs_prev_tab']);
		}
	}
</script>

<style lang="less" scoped>
	@import "../css/_vars";

	.tabs {
		&__header {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 40px;

			.tab {
				font-size: 18px;
				color: @color-light-gray;
				margin-right: 22px;
				padding: 12px 0;
				cursor: pointer;

				&:last-child {
					margin-right: 0;
				}

				&._active {
					color: white;
					box-shadow: 0px -1px 0px 0px @color-beige inset;
				}
			}
		}
	}
</style>


